*,*::after,*::before{
  box-sizing: border-box;
}
body{
  margin:0;
  background:linear-gradient(to right,rgb(18, 231, 18),rgb(248, 162, 2)) ;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.calculator-grid{
  display:grid;
  margin-top:2rem;
  justify-content: center;
  grid-template-columns: repeat(4,6rem);
  grid-template-rows: minmax(7rem,auto) repeat(6,6rem);
}
.calculator-grid>button{
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid rgb(107, 116, 163);
  background-color: rgba(255,255,255,0.75);
}
.calculator-grid>button:hover,.calculator-grid>button:focus{
  background-color: rgba(255,255,255,0.9);
}
.span-two{
  grid-column: span 2;
}
.output{
  grid-column: 1/-1;
  background-color:rgb(22, 1, 1) ;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  padding:0.75rem;
  word-wrap: break-word;
  word-break: break-all;
}
.previous-operand{
  color: whitesmoke;
  font-size:1.5rem;
}
.current-operand{
  color:whitesmoke;
  font-size: 2.5rem;
}
